const bg0 = require('../assets/images/bg_quiz_0.jpg');
const bg1 = require('../assets/images/bg_quiz_1.jpg');
const bg2 = require('../assets/images/bg_quiz_2.jpg');
const bg3 = require('../assets/images/bg_quiz_3_b.jpg'); 
const bg4 = require('../assets/images/bg_quiz_4.jpg');
const bg5 = require('../assets/images/bg_quiz_5.jpg');
const bg6 = require('../assets/images/bg_quiz_6.jpg');
const bg7 = require('../assets/images/bg_quiz_7.jpg');
const loader = require('../assets/images/loader.gif');
import MailchimpSubscribe from '~/node_modules/vue-mailchimp-subscribe';
export default {  
  components: {
    MailchimpSubscribe,
  },
  data() {
    return {
      bg0: bg0,
      bg1: bg1,
      bg2: bg2,
      bg3: bg3,
      bg4: bg4,
      bg5: bg5,
      bg6: bg6,
      bg7: bg7,
      sliderActive: '',
      swiperCittaOptions: {
        slidesPerView: 4,
        spaceBetween: 30,
        loop: false,
        centeredSlides: true,
        pagination: {
            el: '.swiper-pagination-citta',
            clickable: true,
        },
        breakpoints: {
            1024: {
            slidesPerView: 4,
            spaceBetween: 40
            },
            768: {
            slidesPerView: 3,
            spaceBetween: 30
            },
            640: {
            slidesPerView: 2,
            spaceBetween: 20
            },
            320: {
            slidesPerView: 1,
            spaceBetween: 10
            }
        },
          on: {
            init: (i) => {
              if(i.activeIndex === 0){
                this.quiz.city = 'Milano'
              }
            },
            slideChange: (i) => {
              if(i.activeIndex === 0){
                this.quiz.city = 'Milano'
              }
              if(i.activeIndex === 1){
                this.quiz.city = 'Roma'
              }
              if(i.activeIndex === 2){
                this.quiz.city = 'Firenze'
              }
              if(i.activeIndex === 3){
                this.quiz.city = 'Napoli'
              }
            }
          }
      },
      showMap: true,
      quiz: {
        birthday: null,
        city: null,
        type: [],
        occasione: [],
        location: null
      },
      hasCookie: false,
      removedCookie: false,
      type: {
        type_1: false,
        type_2: true,
        type_3: true,
        type_4: true
      },
      occ: {
        type_1: false,
        type_2: false,
        type_3: false,
        type_4: false
      },
      locationC: {
        type_1: false,
        type_2: false,
      },
      quizStep: {
        stepIntro: false,
        stepAge: false,
        stepCity: false,
        stepType: false,
        stepOccasione: false,
        stepLocation: false,
        noNewsletter: false,
        skip: false,
        completed: false
      },
      filtered:null,
    }
  },
  computed: {
      locali() {
        return this.$store.state.content.locali;
      },
      year(){
        return new Date().getFullYear() - 18;
      },
      swiper() {
        this.$refs.sceltacitta.$swiper;
      }
  },
  created() {
    // this.$store.dispatch("locali");
  },
  methods: {
    onError() {
    },

    onSuccess() {
    }, 
    city: function(){
      this.quizStep.stepCity = true;
      this.$store.commit("updateLocali", this.filterCity());
    },
    filterCity: function(){
      let locals = this.locali.filter(item => item.acf.citta.name === this.quiz.city)
      .map(({ id, slug, title, acf }) => ({
          id,
          slug,
          title,
          acf
      }))
      return locals
    },
    location: function(){
      this.quizStep.stepLocation = true;
      this.$store.commit("updateLocali", this.filterLocation());
    },
    filterLocation: function(){
      let locals = this.locali.filter(item => item.acf.location === this.quiz.location)
      .map(({ id, slug, title, acf }) => ({
          id,
          slug,
          title,
          acf
      }))
      return locals
    },
    tipologia: function(){
      this.quizStep.stepType = true;
      this.$store.commit("updateLocali", this.filterType());
    },
    filterType: function(){
      var tipologie = this.quiz.type;
      var locali = [];
      var i;
      for (i = 0; i < this.locali.length; i++) {
        if(tipologie.some(r=> this.locali[i].acf.tipo.includes(r))){
          locali.push(this.locali[i]);
        }
      }
      let locals = locali.map(({ id, slug, title, acf }) => ({
          id,
          slug,
          title,
          acf
      }))
      return locals
    },
    occasione: function(){
      this.quizStep.stepOccasione = true;
      this.$store.commit("updateLocali", this.filterOccasione());
    },
    filterOccasione: function(){
      var occasione = this.quiz.occasione;
      var locali = [];
      var i;
      for (i = 0; i < this.locali.length; i++) {
        if(occasione.some(r=> this.locali[i].acf.occasione_b.includes(r))){
          locali.push(this.locali[i]);
        }
      }
      let locals = locali.map(({ id, slug, title, acf }) => ({
          id,
          slug,
          title,
          acf
      }))
      return locals
    },
    setCookie: function(){
      this.$cookies.set('completed',true,'10d');  
      this.$cookies.set('quiz',this.quiz,'10d');  
    },
    setCookieNewsletter: function(){
      this.$cookies.set('newsletter',true,'90d');  
    },
    removeCookie: function(){
      this.$cookies.remove('quiz');  
      this.$cookies.remove('completed');  
      this.$cookies.remove('firstTime');  
      this.removedCookie = true; 
    },
    localiUpdate: function(){
      this.city();
      this.location();
      this.tipologia();
      this.occasione();
      this.showMap = true;
    }
  },
  beforeUpdate(){
  },
  mounted(){
    if(this.$cookies.get("completed")){
      this.hasCookie = true; 
    }
    if(this.$cookies.get("quiz")){
      this.quiz = this.$cookies.get("quiz"); 
    }
    if(!this.$cookies.get("firstTime")){
      this.$cookies.set('firstTime',true,'10d');  
    }
       
  }
}
